.modal {
  position: absolute;
  display: none;
  flex-direction: column;
  height: fit-content;
  width: 12rem;
  background: #FFFFFF;
  border-radius: 8px;
  padding: 1rem 0;
  box-sizing: border-box;
  box-shadow: 0px 0px 15px #999999;
  z-index: 2000;
  margin: 0;
}
.modal_top {
  top: 5rem;
  left: 1rem;
}
.modal_top-left {
  top: 7rem;
  right: 0;
}
.modal_bottom {
  bottom: 5rem;
  right: 0;
}
.modal__row {
  cursor: pointer;
  display: flex;
  align-items: center;
  width: 100%;
  height: 2.5rem;
  padding: 0 1.75rem;
  box-sizing: border-box;
  text-decoration: none;
}
.modal__row:visited {
  color: #000000;
}
.modal__row:hover {
  background: rgba(0, 0, 0, 0.04);
}
.modal__row:active {
  background: rgba(0, 0, 0, 0.08);
}
.modal__row__icon {
  width: 24px;
  height: 24px;
  background: transparent;
  margin-right: 1rem;
}
